"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import {
  Search,
  Users,
  Eye,
  Ban,
  UserX,
  MessageSquare,
  Clock,
  MapPin,
  Coins,
  Package,
  Shield,
  AlertTriangle,
  Volume2,
  VolumeX,
  RotateCcw
} from "lucide-react"
import { mockPlayers, predefinedReasons, type Player, type Punishment } from "@/lib/minecraft-data"

export default function PlayersPage() {
  const [players, setPlayers] = useState(mockPlayers)
  const [searchTerm, setSearchTerm] = useState("")
  const [selectedPlayer, setSelectedPlayer] = useState<Player | null>(null)
  const [punishmentDialog, setPunishmentDialog] = useState(false)
  const [punishmentType, setPunishmentType] = useState<'ban' | 'kick' | 'mute' | 'warn'>('warn')
  const [punishmentReason, setPunishmentReason] = useState("")
  const [punishmentDuration, setPunishmentDuration] = useState("")

  const filteredPlayers = players.filter(player =>
    player.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
    player.uuid.includes(searchTerm)
  )

  const onlinePlayers = filteredPlayers.filter(p => p.isOnline)
  const offlinePlayers = filteredPlayers.filter(p => !p.isOnline)

  const getRankColor = (rank: string) => {
    switch (rank) {
      case 'owner': return 'bg-red-600'
      case 'admin': return 'bg-purple-600'
      case 'moderator': return 'bg-blue-600'
      case 'vip': return 'bg-yellow-600'
      default: return 'bg-gray-600'
    }
  }

  const formatPlaytime = (minutes: number) => {
    const hours = Math.floor(minutes / 60)
    const days = Math.floor(hours / 24)
    if (days > 0) return `${days}d ${hours % 24}h`
    return `${hours}h ${minutes % 60}m`
  }

  const formatLastSeen = (date: Date) => {
    const now = new Date()
    const diff = now.getTime() - date.getTime()
    const minutes = Math.floor(diff / 60000)
    
    if (minutes < 1) return 'Agora'
    if (minutes < 60) return `${minutes}m atrás`
    const hours = Math.floor(minutes / 60)
    if (hours < 24) return `${hours}h atrás`
    const days = Math.floor(hours / 24)
    return `${days}d atrás`
  }

  const handlePunishment = () => {
    if (!selectedPlayer) return

    const newPunishment: Punishment = {
      id: Date.now().toString(),
      type: punishmentType,
      reason: punishmentReason,
      staffMember: 'Admin',
      date: new Date(),
      duration: punishmentDuration ? parseInt(punishmentDuration) : undefined,
      isActive: true
    }

    setPlayers(prev => prev.map(player => 
      player.id === selectedPlayer.id 
        ? { ...player, punishments: [...player.punishments, newPunishment] }
        : player
    ))

    setPunishmentDialog(false)
    setPunishmentReason("")
    setPunishmentDuration("")
  }

  const PlayerCard = ({ player }: { player: Player }) => (
    <Card className="bg-gray-800 border-gray-700">
      <CardContent className="p-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <div className="relative">
              <Avatar className="h-12 w-12">
                <AvatarImage src={player.avatar} />
                <AvatarFallback>{player.username[0]}</AvatarFallback>
              </Avatar>
              {player.isOnline && (
                <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-gray-800" />
              )}
            </div>
            <div>
              <div className="flex items-center space-x-2">
                <h3 className="font-medium text-white">{player.username}</h3>
                <Badge className={getRankColor(player.rank)} variant="secondary">
                  {player.rank}
                </Badge>
                {player.isOnline && (
                  <Badge variant="secondary" className="bg-green-600">
                    Online
                  </Badge>
                )}
              </div>
              <p className="text-sm text-gray-400">
                {player.isOnline ? `${player.location.world} (${player.location.x}, ${player.location.y}, ${player.location.z})` : `Visto ${formatLastSeen(player.lastSeen)}`}
              </p>
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            <Dialog>
              <DialogTrigger asChild>
                <Button size="sm" variant="ghost" onClick={() => setSelectedPlayer(player)}>
                  <Eye className="h-4 w-4" />
                </Button>
              </DialogTrigger>
              <DialogContent className="bg-gray-800 border-gray-700 max-w-2xl">
                <DialogHeader>
                  <DialogTitle className="text-white flex items-center space-x-2">
                    <Avatar className="h-8 w-8">
                      <AvatarImage src={player.avatar} />
                      <AvatarFallback>{player.username[0]}</AvatarFallback>
                    </Avatar>
                    <span>{player.username}</span>
                  </DialogTitle>
                </DialogHeader>
                
                <Tabs defaultValue="info" className="w-full">
                  <TabsList className="grid w-full grid-cols-4 bg-gray-700">
                    <TabsTrigger value="info">Info</TabsTrigger>
                    <TabsTrigger value="punishments">Punições</TabsTrigger>
                    <TabsTrigger value="inventory">Inventário</TabsTrigger>
                    <TabsTrigger value="chat">Chat</TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="info" className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">UUID</p>
                        <p className="text-sm text-white font-mono">{player.uuid}</p>
                      </div>
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">Rank</p>
                        <Badge className={getRankColor(player.rank)}>{player.rank}</Badge>
                      </div>
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">Tempo de Jogo</p>
                        <p className="text-sm text-white">{formatPlaytime(player.playtime)}</p>
                      </div>
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">Saldo</p>
                        <p className="text-sm text-white">R$ {player.balance.toFixed(2)}</p>
                      </div>
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">Localização</p>
                        <p className="text-sm text-white">
                          {player.location.world} ({player.location.x}, {player.location.y}, {player.location.z})
                        </p>
                      </div>
                      <div className="space-y-2">
                        <p className="text-sm text-gray-400">Status</p>
                        <Badge variant={player.isOnline ? "default" : "secondary"}>
                          {player.isOnline ? "Online" : `Offline - ${formatLastSeen(player.lastSeen)}`}
                        </Badge>
                      </div>
                    </div>
                  </TabsContent>
                  
                  <TabsContent value="punishments">
                    <ScrollArea className="h-64">
                      <div className="space-y-2">
                        {player.punishments.length === 0 ? (
                          <p className="text-gray-400 text-center py-4">Nenhuma punição registrada</p>
                        ) : (
                          player.punishments.map((punishment) => (
                            <div key={punishment.id} className="p-3 bg-gray-700 rounded">
                              <div className="flex justify-between items-start">
                                <div>
                                  <Badge variant={punishment.type === 'ban' ? 'destructive' : 'secondary'}>
                                    {punishment.type.toUpperCase()}
                                  </Badge>
                                  <p className="text-sm text-white mt-1">{punishment.reason}</p>
                                  <p className="text-xs text-gray-400">
                                    Por {punishment.staffMember} em {punishment.date.toLocaleDateString()}
                                  </p>
                                </div>
                                <Badge variant={punishment.isActive ? 'destructive' : 'secondary'}>
                                  {punishment.isActive ? 'Ativo' : 'Inativo'}
                                </Badge>
                              </div>
                            </div>
                          ))
                        )}
                      </div>
                    </ScrollArea>
                  </TabsContent>
                  
                  <TabsContent value="inventory">
                    <div className="text-center py-8">
                      <Package className="h-12 w-12 text-gray-400 mx-auto mb-2" />
                      <p className="text-gray-400">Inventário será carregado aqui</p>
                    </div>
                  </TabsContent>
                  
                  <TabsContent value="chat">
                    <ScrollArea className="h-64">
                      <div className="space-y-2">
                        {player.chatHistory.length === 0 ? (
                          <p className="text-gray-400 text-center py-4">Nenhuma mensagem recente</p>
                        ) : (
                          player.chatHistory.map((message) => (
                            <div key={message.id} className="p-2 bg-gray-700 rounded">
                              <p className="text-sm text-white">{message.message}</p>
                              <p className="text-xs text-gray-400">
                                {message.world} - {message.timestamp.toLocaleString()}
                              </p>
                            </div>
                          ))
                        )}
                      </div>
                    </ScrollArea>
                  </TabsContent>
                </Tabs>
              </DialogContent>
            </Dialog>

            <Dialog open={punishmentDialog} onOpenChange={setPunishmentDialog}>
              <DialogTrigger asChild>
                <Button 
                  size="sm" 
                  variant="ghost" 
                  className="text-red-400 hover:text-red-300"
                  onClick={() => setSelectedPlayer(player)}
                >
                  <Shield className="h-4 w-4" />
                </Button>
              </DialogTrigger>
              <DialogContent className="bg-gray-800 border-gray-700">
                <DialogHeader>
                  <DialogTitle className="text-white">Aplicar Punição</DialogTitle>
                  <DialogDescription className="text-gray-400">
                    Aplicar punição para {selectedPlayer?.username}
                  </DialogDescription>
                </DialogHeader>
                
                <div className="space-y-4">
                  <div>
                    <label className="text-sm text-gray-300">Tipo de Punição</label>
                    <Select value={punishmentType} onValueChange={(value: any) => setPunishmentType(value)}>
                      <SelectTrigger className="bg-gray-700 border-gray-600">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent className="bg-gray-700 border-gray-600">
                        <SelectItem value="warn">Advertência</SelectItem>
                        <SelectItem value="mute">Mute</SelectItem>
                        <SelectItem value="kick">Kick</SelectItem>
                        <SelectItem value="ban">Ban</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  
                  <div>
                    <label className="text-sm text-gray-300">Motivo</label>
                    <Select value={punishmentReason} onValueChange={setPunishmentReason}>
                      <SelectTrigger className="bg-gray-700 border-gray-600">
                        <SelectValue placeholder="Selecione um motivo" />
                      </SelectTrigger>
                      <SelectContent className="bg-gray-700 border-gray-600">
                        {predefinedReasons.map((reason) => (
                          <SelectItem key={reason} value={reason}>{reason}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  
                  {(punishmentType === 'ban' || punishmentType === 'mute') && (
                    <div>
                      <label className="text-sm text-gray-300">Duração (minutos)</label>
                      <Input
                        type="number"
                        placeholder="Deixe vazio para permanente"
                        value={punishmentDuration}
                        onChange={(e) => setPunishmentDuration(e.target.value)}
                        className="bg-gray-700 border-gray-600"
                      />
                    </div>
                  )}
                </div>
                
                <DialogFooter>
                  <Button variant="ghost" onClick={() => setPunishmentDialog(false)}>
                    Cancelar
                  </Button>
                  <Button 
                    onClick={handlePunishment}
                    className="bg-red-600 hover:bg-red-700"
                    disabled={!punishmentReason}
                  >
                    Aplicar Punição
                  </Button>
                </DialogFooter>
              </DialogContent>
            </Dialog>
          </div>
        </div>
      </CardContent>
    </Card>
  )

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-white">Gestão de Jogadores</h1>
          <p className="text-gray-400">Gerencie jogadores, visualize informações e aplique punições</p>
        </div>
        <div className="flex items-center space-x-2">
          <Badge variant="secondary" className="bg-green-600">
            {onlinePlayers.length} Online
          </Badge>
          <Badge variant="secondary">
            {players.length} Total
          </Badge>
        </div>
      </div>

      {/* Search */}
      <Card className="bg-gray-800 border-gray-700">
        <CardContent className="p-4">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
            <Input
              placeholder="Buscar por nome de usuário ou UUID..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10 bg-gray-700 border-gray-600"
            />
          </div>
        </CardContent>
      </Card>

      {/* Players Tabs */}
      <Tabs defaultValue="online" className="w-full">
        <TabsList className="grid w-full grid-cols-2 bg-gray-800">
          <TabsTrigger value="online" className="data-[state=active]:bg-gray-700">
            Jogadores Online ({onlinePlayers.length})
          </TabsTrigger>
          <TabsTrigger value="all" className="data-[state=active]:bg-gray-700">
            Todos os Jogadores ({filteredPlayers.length})
          </TabsTrigger>
        </TabsList>
        
        <TabsContent value="online" className="space-y-4">
          {onlinePlayers.length === 0 ? (
            <Card className="bg-gray-800 border-gray-700">
              <CardContent className="p-8 text-center">
                <Users className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-400">Nenhum jogador online no momento</p>
              </CardContent>
            </Card>
          ) : (
            <div className="grid gap-4">
              {onlinePlayers.map((player) => (
                <PlayerCard key={player.id} player={player} />
              ))}
            </div>
          )}
        </TabsContent>
        
        <TabsContent value="all" className="space-y-4">
          <div className="grid gap-4">
            {filteredPlayers.map((player) => (
              <PlayerCard key={player.id} player={player} />
            ))}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
